@import "../../index";

$bars-toggle-width: 768px;

.nav {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem calc((100vw - 1000px) / 2);
    z-index: 10;
    font-family: $font-family-mono;
    transition: $transition;
}

.nav-brand {
    color: $foreground-secondary;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;

    img {
        height: 100%;
        width: auto;
    }
}

.nav-bars {
    display: none;

    @media screen and (max-width: $bars-toggle-width) {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 90%);
        font-size: $fz-icon-lg;
        color: $foreground-tertiary;
        cursor: pointer;
    }
}

.nav-menu {
    display: flex;
    align-items: center;

    @media screen and (max-width: $bars-toggle-width) {
        display: none;
    }

    counter-reset: nav-menu-item 0;

    .nav-link {
        color: $foreground-secondary;
        display: flex;
        align-items: center;
        text-decoration: none;
        padding: 0 1rem;
        height: 100%;
        cursor: pointer;
        transition: $transition;
        font-size: $fz-sm;

        &:hover {
            color: $foreground-tertiary;
        }

        &::before {
            content: "0" counter(nav-menu-item) ".";
            color: $foreground-tertiary;
            margin-right: 5px;

            counter-increment: nav-menu-item 1;
        }
    }
}